import React from 'react';
import { Formik, Field } from 'formik';
import * as Yup from 'yup';
import $ from 'jquery';

class ContactForm extends React.Component {
  constructor(props) {
    super(props);
    this.contactForm = React.createRef();
  }
  render() {
    return (
      <Formik mapPropsToValues={(props) => { return {name: '', email: '', contactField: '',} }}
              initalValues={{name: '', email: '', contactField: ''}}
              onSubmit={(values, {setSubmitting}) => {
                // What happens on submit
                setTimeout(() => {
                  var vm = this.contactForm.current;
                  var jsonValues = JSON.stringify(values)

                  $.ajax({
                    type: "POST",
                    url: 'https://julianm-backend.cf/contact-form-json.php',
                    data: {contactData:jsonValues},
                    error: function (data) {
                      console.log("Error");
                      vm.classList.remove('form-success');
                      vm.classList.add('form-error');
                      vm.style.display = "block";
                      vm.innerHTML = "There was an error sending the request. Please try again."
                    },
                    success: function (e) {
                      console.log("Finished");
                      vm.classList.remove('form-error');
                      vm.classList.add('form-success');
                      vm.style.display = "block";
                      vm.innerHTML = "Your message was successfully sent."
                      values = {name: '', email: '', message: ''};
                    }
                  })

                  /*
                  var xhr = new XMLHttpRequest();
                  xhr.open('POST', 'https://julianm-backend.cf/contact-form-json.php', true);

                  xhr.onload = function () {
                    console.log("Finished");
                    vm.classList.remove('form-error');
                    vm.classList.add('form-success');
                    vm.style.display = "block";
                    vm.innerHTML = "Your message was successfully sent."
                    values = {name: '', email: '', message: ''};
                  }
                  xhr.onerror = function () {
                    console.log("Error");
                    vm.classList.remove('form-success');
                    vm.classList.add('form-error');
                    vm.style.display = "block";
                    vm.innerHTML = "There was an error sending the request. Please try again."
                  }

                  console.log(JSON.stringify(values));
                  xhr.send(JSON.stringify(values));
                  */

                  /*
                  $.post('http://julianm-backend.cf/contact-form.php', values, null, "json").done(function(response) {
                    vm.classList.remove('form-error');
                    vm.classList.add('form-success');
                    vm.style.display = "block";
                    vm.innerHTML = "Your message was successfully sent."
                    values = {name: '', email: '', contactField: ''};
                  }).fail(function(response){
                    vm.classList.remove('form-success');
                    vm.classList.add('form-error');
                    vm.style.display = "block";
                    vm.innerHTML = "There was an error sending the request. Please try again."
                  });
                  setSubmitting(false); */
                }, 400);
              }}
              validationSchema={Yup.object().shape({
                name: Yup.string().required('Required'),
                email: Yup.string().email().required('Required'),
                message: Yup.mixed().required('Required'),
              })}
              className="grid-right"
      >
        {props => {
          const {values, touched, errors, handleChange, handleBlur, handleSubmit} = props;
          return (
            <form name="contactForm" className="contact-form" action="https://julianm-backend.cf/contact-form-json.php" onSubmit={handleSubmit}>
              <input type="text" value={values.name} name="name" placeholder="Name" onChange={handleChange} onBlur={handleBlur}/>
              {errors.name && touched.name && (<div className="field-error">Please enter your name.</div>)}
              <input type="text" value={values.email} name="email" placeholder="Email" onChange={handleChange} onBlur={handleBlur}/>
              {errors.email && touched.email && (<div className="field-error">Please enter a valid email address.</div>)}
              <Field component="textarea" value={values.message} name="message" placeholder="Message" onChange={handleChange} onBlur={handleBlur} />
              {errors.message && touched.message && (<div className="field-error">Oops! Looks like you forgot to write a message!</div>)}
              <input type="submit" name="Send"/>
              <div className="form-validation" ref={this.contactForm}></div>
            </form>
          )
        }}
      </Formik>
    );
  }
}

export default ContactForm;
